*{margin:0;padding:0;box-sizing:border-box;font-family:"Comic Sans MS","Comic Sans","Chocolate Classical Sans";font-weight:400;font-style:normal}html,body{width:100vw;height:100vh;margin:0;padding:0;overflow:hidden;background-image:url(./assets/picture/BG/背景-活動中心.png);background-size:cover;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;flex-direction:column}.container,.centered-container{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center;width:100%;height:100%}.centered-image{width:100%;height:auto;object-fit:cover;border:2% solid #fff;border-radius:2%;box-shadow:0 1vh 2vh rgba(0,0,0,.2)}.startButton{width:30vw;height:15vh;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background:none;border:none;border-radius:1vw;cursor:pointer;transition:transform .2s}.startButton img{width:100%;height:100%;border-radius:1vw}.startButton:active{transform:translate(-50%, -50%) scale(0.95)}.intro-container{background-color:rgba(255,255,255,.95);padding:2vw;border-radius:1vw;text-align:center;box-shadow:0 .5vw 1vw rgba(0,0,0,.1);font-size:1.5vw;color:#333;max-height:60vh;overflow-y:auto;margin:2vh}.intro-container p{margin-bottom:1.5vh;line-height:1.6}.intro-container button{padding:1vw 2vw;background-color:#fc0;color:#333;border:none;border-radius:.5vw;cursor:pointer;transition:background-color .3s,transform .3s;margin-top:1.5vh;font-size:1.5vw}.intro-container button:hover{background-color:#fd3;transform:scale(1.05)}@media screen and (max-width: 1024px){.startButton{width:30vw;height:20vh;margin-top:5%}.intro-container{font-size:2.5vw}.intro-container button{font-size:2vw}}@media screen and (max-width: 768px){.startButton{width:40vw;height:20vh;margin-top:5%}.intro-container{font-size:3vw}.intro-container button{font-size:2vw}}@media screen and (max-width: 480px){.startButton{width:50vw;height:25vh;margin-top:5%}.intro-container{font-size:4vw}.intro-container button{font-size:4vw}}@media screen and (orientation: portrait){html,body{flex-direction:column}}